body {
  background-color: #232931;
}
.App {
  background-color: #393e46;
  min-height: 600px;
  width: 550px;
  margin: 40px auto;
}

#to-do-form input {
  background-color: rgb(223, 228, 228);
  border: 0;
  color: #000000;
  width: 375px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder {
  color: #2b2c2c;
}
#to-do-form input:focus {
  outline: none;
}
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: #d7dde6;
  cursor: pointer;
}
#to-do-form button:focus {
  outline: none;
}
